/* eslint-disable no-console */

import { register } from "register-service-worker";

if (process.env.NODE_ENV === "production") {
  register(`${process.env.BASE_URL}service-worker.js`, {
    ready() {
      console.log(
        "registerServiceWorker - " +
          "App is being served from cache by a service worker.\n" +
          "For more details, visit https://goo.gl/AFskqB"
      );
    },
    registered() {
      console.log(
        "registerServiceWorker - " + "Service worker has been registered."
      );
    },
    cached() {
      console.log(
        "registerServiceWorker - " + "Content has been cached for offline use."
      );
    },
    updatefound() {
      console.log("registerServiceWorker - " + "New content is downloading.");
    },
    updated() {
      console.log(
        "registerServiceWorker - " + "New content is available; please refresh."
      );
    },
    offline() {
      console.log(
        "registerServiceWorker - " +
          "No internet connection found. App is running in offline mode."
      );
    },
    error(error) {
      console.error("Error during service worker registration:", error);
    },
  });
}
