import { Constants } from "@/constants";

export class SettingsService {
  public actionAtEnd: string;
  public timeRemaning: number;
  public soundOn = false;
  public changeColorOn = true;
  public countsBeforeAds: number;
  public timesPerMinute = 160;
  public accentBetweenTicks = 4;

  constructor() {
    this.actionAtEnd = Constants.ActionsAtEnd.DEFAULT;
    this.timeRemaning = Constants.TIMEOUT_SECONDS;
    this.countsBeforeAds = Constants.COUNTS_BEFORE_ADS;
    this.timesPerMinute = Constants.TIMES_PER_MINUTE_DEFAULT;
    this.accentBetweenTicks = Constants.ACCENT_BETWEEN_TICKS_DEFAULT;
  }

  setTimeout(seconds: number): void {
    this.timeRemaning = seconds;
    console.log("settings - this.timeout", this.timeRemaning);
  }

  setActionAtEnd(action: string): void {
    this.actionAtEnd = action;
    console.log("settings - this.actionAtEnd", this.actionAtEnd);
  }

  setSoundOn(soundOn: boolean): void {
    this.soundOn = soundOn;
    console.log("settings - this.soundOn", this.soundOn);
  }

  setChangeColorOn(changeColorOn: boolean): void {
    this.changeColorOn = changeColorOn;
    console.log("settings - this.changeColorOn", this.changeColorOn);
  }

  setTimesPerMinute(timesPerMinute: number): void {
    this.timesPerMinute = timesPerMinute;
    console.log("settings - this.timesPerMinute", this.timesPerMinute);
  }

  setCountsBeforeAds(countsBeforeAds: number): void {
    this.countsBeforeAds = countsBeforeAds;
    console.log("settings - this.countsBeforeAds", this.countsBeforeAds);
  }

  setAccentBetweenTicks(accentBetweenTicks: number): void {
    this.accentBetweenTicks = accentBetweenTicks;
    console.log("settings - this.accentBetweenTicks", this.accentBetweenTicks);
  }
}
export const settings = new SettingsService();
