export type SoundValue = {
  key: string;
  asset: string;
  type: string;
};

export class SoundsService {
  static HTML5 = "html5";
  static NATIVE = "native";
  audioType: string = SoundsService.HTML5;
  sounds: SoundValue[] = [];

  // constructor(
  //   public nativeAudio: NativeAudio,
  //   platform: Platform,
  //   private console: ConsoleService,
  // ) {
  //   if (platform.is('cordova')) {
  //     this.audioType = SoundsService.NATIVE;
  //   }
  // }

  preload(key: string, asset: string): void {
    if (this.audioType === SoundsService.HTML5) {
      const audio = {
        key: key,
        asset: asset,
        type: SoundsService.HTML5,
      };

      this.sounds.push(audio);
    } else {
      // this.nativeAudio.preloadSimple(key, asset);
      // const audio = {
      //   key: key,
      //   asset: key,
      //   type: SoundsService.NATIVE,
      // };
      // this.sounds.push(audio);
    }
  }

  play(key: string, volumen = 1): void {
    const audio = this.sounds.find((sound) => {
      return sound.key === key;
    });
    if (!audio) {
      return;
    }
    if (audio.type === SoundsService.HTML5) {
      const audioAsset = new Audio(audio.asset);
      audioAsset.volume = volumen;
      audioAsset.play();
    } else {
      // this.nativeAudio.play(audio.asset).then(
      //   (res) => this.console.log(res),
      //   (err) => this.console.log(err),
      // );
    }
  }
}

export const sounds = new SoundsService();
