export class ColorsService {
  colors: string[];
  buffer: string[];
  beforeRandomColor = "";
  currentRandomColor = "";
  colorCallback?: (color: string) => void;

  constructor(colorCallback?: (color: string) => void) {
    this.colorCallback = colorCallback;
    this.colors = [];
    this.buffer = this.colors;
    this.loadData();
  }

  loadData(): void {
    this.colors = [
      "custom-red",
      "custom-pink",
      "custom-purple",
      "custom-deep-purple",
      "custom-indigo",
      "custom-blue",
      "custom-light-blue",
      "custom-cyan",
      "custom-teal",
      "custom-green",
      "custom-light-green",
      "custom-lime",
      "custom-yellow",
      "custom-amber",
      "custom-orange",
      "custom-deep-orange",
      "custom-brown",
      "custom-grey",
      "custom-blue-grey",
    ];
  }

  reset(): void {
    this.buffer = this.colors;
  }

  all(): string[] {
    return this.colors;
  }

  next(): string {
    if (this.buffer.length === 0) {
      this.reset();
    }

    const randomColor = this.buffer[
      Math.floor(Math.random() * this.buffer.length)
    ];
    this.buffer = this.buffer.filter((word) => word !== randomColor);

    this.saveBefore(randomColor);

    console.debug("colorservice - randomColor", randomColor);
    return randomColor;
  }

  before(): string {
    return this.beforeRandomColor;
  }

  saveBefore(randomColor: string): void {
    this.beforeRandomColor = this.currentRandomColor;
    this.currentRandomColor = randomColor;
    this.callColorCallback();
  }

  private callColorCallback(): void {
    if (this.colorCallback) {
      this.colorCallback(this.currentRandomColor);
    }
  }
}

export const colors = new ColorsService();
