import { settings } from "@/data/settings";

export class Timer {
  time: number = settings.timeRemaning;
  runTimer = true;
  hasStarted = true;
  hasFinished = false;
  remainingTime = settings.timeRemaning;
  finishCallback?: (finished: boolean) => void;
  tickEvent?: (remainingTime: number) => void;

  constructor(
    finishCallback: (finished: boolean) => void,
    tickEvent: (remainingTime: number) => void
  ) {
    this.finishCallback = finishCallback;
    this.tickEvent = tickEvent;
  }

  initTimer(): void {
    console.info("settings - initTimer");
    this.remainingTime = settings.timeRemaning;
    this.time = this.remainingTime;
    this.runTimer = false;
    this.hasStarted = false;
    this.hasFinished = false;
    this.callFinishCallback();
  }

  startTimer(): void {
    console.info("settings - startTimer");
    this.runTimer = true;
    this.hasStarted = true;
    this.timerTick();
  }

  pauseTimer(): void {
    console.info("settings - pauseTimer");
    this.runTimer = false;
  }

  resetTimer(): void {
    console.info("settings - resetTimer");
    this.remainingTime = settings.timeRemaning;
    this.callFinishCallback();
    this.callTickEvent();
    this.time = this.remainingTime;
    if (this.hasFinished) {
      this.initTimer();
    }
    if (!this.runTimer) {
      this.hasFinished = false;
      this.callFinishCallback();
      this.startTimer();
    }
  }

  resumeTimer(): void {
    console.info("settings - resumeTimer");
    this.startTimer();
  }

  timerTick(): void {
    console.info("settings - timerTick");
    setTimeout(() => {
      if (!this.runTimer) {
        return;
      }
      this.remainingTime--;
      this.callTickEvent();
      if (this.remainingTime > 0) {
        this.timerTick();
      } else {
        this.hasFinished = true;
        this.callFinishCallback();
        console.info("settings - finish");
      }
    }, 1000);
  }

  private callFinishCallback(): void {
    if (this.finishCallback) {
      this.finishCallback(this.hasFinished);
    }
  }

  private callTickEvent(): void {
    if (this.tickEvent) {
      this.tickEvent(this.remainingTime);
    }
  }
}
